import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { FontAwesomeModule, FaIconLibrary} from '@fortawesome/angular-fontawesome';
import { faGithub, faCodepen, faInstagram, faYoutube, faLinkedin, faMedium, faFacebook } from '@fortawesome/free-brands-svg-icons';
import { NavMenuComponent } from './component/nav-menu/nav-menu.component';
import { ResumeComponent } from './component/resume/resume.component';
import { HomeComponent } from './component/home/home.component';

const appRoutes: Routes=[
  { path:"", component:HomeComponent},
  { path:"resume", component:ResumeComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    NavMenuComponent,
    ResumeComponent,
    HomeComponent
  ],
  imports: [
    BrowserModule,
    FontAwesomeModule,
    AppRoutingModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing:true}
    )
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {
  constructor(library: FaIconLibrary){
    //library.addIconPacks(fas);
    library.addIcons(faGithub, faCodepen, faInstagram, faYoutube, faLinkedin, faMedium, faFacebook);
  }
 }
